footer {
  height: 3em;
  /* line-height: 5em; */
  background-color: #222;
  color: #ececec;
}

@media (max-width: 768px) {
  footer p {
    vertical-align: middle;
    margin: 0 0.5em;
    display: inline-block;
  }
}

@media (min-width: 768px) {
footer p {
  vertical-align: middle;
  margin: 1em 0.5em;
  display: inline-block;
  }
}

footer a {
  color: rgb(128, 128, 240);
}

footer a:visited {
  color: rgb(186, 97, 249);
}



