.color-box {
  position: relative;
  margin: 0 auto;
  margin-top: 5%;
  background-color: #ebebeb;
  border-radius: 30px;
  width: 75%;
  height: 60%;
  -webkit-transition: background-color 0.75s;
  -o-transition: background-color 0.75s;
  transition: background-color 0.75s;
}

.color-box h3 {
  text-align: center;
  display: block;
  position: absolute;
  font-weight: 100;
  font-size: 2em;
  top: 50%;
  width: 100%;
}



