.color-pill {
  position: relative;
  display: inline-block;
  width: 5%;
  margin: 5em 4%;
  height: 70%;
  -webkit-transition: opacity 0.75s ease-in;
  -o-transition: opacity 0.75s ease-in;
  transition: opacity 0.75s ease-in;
}

.pill-box {
  width: 100%;
  height: 70%;
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  z-index: 5;
}

.pill-filling {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-bottom-right-radius: 50px;
  border-bottom-left-radius: 50px;
  z-index: 10;
}

.color-pill p {
  /* text-align: center; */
  margin-left: -100%;
  margin-right: -100%;
  /* font-size: 1.75em; */
  margin-top: 0.5em;
  z-index: 20;
}



