.App {
  /* These settings are used to create the sticky footer */
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.color-text {
  font-size: 5em;
  margin-top: 0.5em;
  -webkit-transition: color 0.75s;
  -o-transition: color 0.75s;
  transition: color 0.75s;
}

/* Used to create a sticky footer */
.wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
}



